// Events.js
import eventsData, {Employer, Place, Institution, Broadcast} from "./eventsData";
import YoutubeEmbed from "./YoutubeEmbed"
import VimeoEmbed from "./VimeoEmbed"

const evt = eventsData.reverse();
const Events = evt.map((e) => {
    // console.log("Event", e);
    let title = e.title;
    let text = "...";
    if (e.kind===2) {
        title = "(Sofware Release) => { " + title + " }";
    } else {
        if (e.employer>=1) text += "Together with " + Employer[e.employer];
        if (e.broadcast>=0) { 
            text = "Live stream broadcasted from " + Broadcast[e.broadcast] + " ";
        } else {
            text = "Filmed performance ";
        }
        if (e.institution>=1) text += " at " + Institution[e.institution];
        if (e.location>=1) text += ". " + Place[e.location];
    }
    let id = "id"+e.kind.toString()+e.employer+e.location+e.year+e.month+e.day;
    let description = [];
    if(e.img){
        description.push(
        <div className="Event-gallery-container" key={id+"-img"}>
            <div className="Event-gallery">
                <img 
                className="Event-image"
                src={e.img}
                alt="img"/>
            </div>
            <div className="Event-caption">
                <span className="Event-desc" key={id+"-txt"}>{text}</span>
            </div>
        </div>
            );
    } else {
        if(e.kind===2){
            description.push(
            <div key={id+"-txt"}>
                <a 
                href={e.url}
                title={e.url}
                target="_blank" rel="noreferrer"
                >
                    <span className="Event-desc">{e.url}</span>
                </a>
            </div>
            )
        } else {
            description.push(
                <span className="Event-desc" key={id+"-txt"}>{text}</span>
            );
        }
    }
    if (e.video) {
        let idx=0;
        description.push(e.video.map((i) => {
            idx += 1;
            if ("youtube"===i.hostname) {
                return(
                    <YoutubeEmbed  key={id+"-video-"+i.id+idx} embedId={i.id} />
                );
            } else if ("vimeo"===i.hostname) {
                return(
                    <VimeoEmbed  key={id+"-video-"+i.id+idx} embedId={i.id} />
                );
            } else {
                console.log("Unimplemented embedder", i.hostname);
                return (
                    <a className="Event-video" key={id+"-video-"+i.id+idx} href={i.id} title={"Watch on "+i.hostname} target="_blank" rel="noreferrer">Watch on {i.hostname}</a>
                );
            }
        }));
    }
    // console.log("Description", description);
    // the actual event div structure
    return(
        <div 
            id={id} 
            key={id} 
            className="Event-item"
        >
            <a 
            className="Event-link" 
            key={id+"-url"} 
            href={e.url} title={title} target="_blank" rel="noreferrer"
            >
            <div className="Event-title">
                <code>{title}</code>
            </div>
            </a>
            <div className="Event-date">
                <code>{e.year+" | "+e.month+" | "+e.day}</code>
            </div>
            <div className="Event-text">
                {description}
            </div>
        </div>
    );
});
// console.log(Events);
export default Events;
